import { Swiper, Navigation, Pagination, Scrollbar } from "swiper";

// Install modules
Swiper.use([Navigation, Pagination, Scrollbar]);

const swiper = new Swiper('.brand-slider>.swiper-container', {
  spaceBetween: 30,
  speed: 500,

  // Responsive breakpoints
  breakpoints: {

    // when window width is <= 320px
    320: {
      slidesPerView: 1,
      spaceBetween: 10
    },
    // when window width is <= 480px
    480: {
      slidesPerView: 2,
      spaceBetween: 20
    },

    // when window width is <= 640px
    640: {
      slidesPerView: 5,
      spaceBetween: 30
    }
  } ,

});

