$(".menu-top__link").each(function() {
  if (this.href == window.location.href) {
      $(this).addClass("active");
      } else {
        $(this).removeClass('active');
      }
  });

  $(`#header`).find(`a`).on(`click`, function () {
    const $el = $(this)
    const id = $el.attr(`href`)

    $(`html, body`).animate({
        scrollTop: $(id).offset().top
    }, 600)

    return false
})
